.container-adeudo {
  border-radius: 6px;
  padding: 5px;
  width: 100%;
  padding: 50px;
  padding-top: 10px;
  padding-bottom: 20px;
  display: flex;
  justify-content: center;
  gap: 10px;
  flex-direction: row;
  margin-top: -30px;
}
.options {
  background-color: white;
  width: 400px;
  height: auto;
  padding: 20px;
}
.header1 {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 60px;
}
.title {
  display: flex;
  flex-direction: column;
  flex: 4;
  font-weight: bold;
  font-size: "16px";
  text-align: center;
}
.imagen1 {
  flex: 1;
  height: 100%;
  margin-bottom: 20px;
}
.imagen2 {
  flex: 1;
  height: 100%;
  margin-bottom: 20px;
}
.title {
  font-weight: bold;
}
.content {
  width: "20%";
}
.buttons {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
.buttons div {
  display: flex;
  gap: 10px;
  margin-right: 80px;
}
.print-only {
  display: none;
}

@media print {
  .print-only {
    display: block !important;
  }
}
